/**
 * An event whose default action can be prevented
 * by calling the `preventDefault` method.
 */
export abstract class PreventableEvent {
  private prevented: boolean = false;

  /**
   * Prevents the default action for a specified event.
   *
   * In this way, the source component suppresses
   * the built-in behavior that follows the event.
   */
  public preventDefault(): void {
    this.prevented = true;
  }

  /**
   * Returns `true` if the event was prevented
   * by any of its subscribers.
   *
   * @returns `true` if the default action has been prevented, `false` otherwise
   */
  public isDefaultPrevented(): boolean {
    return this.prevented;
  }
}



